.app-wrapper {
    width: 1400px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 5px;

    height: 100vh;

    grid-template-areas:
    "header header"
    "nav content";
}
.app-wrapper-content {
    grid-area: content;
    background-color: whitesmoke;
}

